.resume-container {
  text-align: center;
  color: white;
  padding: 7rem;
}

.resumeTitle {
  font-family: 'Castoro Titling', cursive;
  margin-bottom: 1rem; /* Add this line */
}

.skills-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.skills-list li {
  margin: 0.5rem;
  padding: 0.5rem;
  border: 1px solid #f9f1f1;
  border-radius: 5px;
  color: white;
}

.download-button {
  padding: 1rem;
  font-size: 1.2rem;
  background-color: #f5faf5;
  color: rgb(20, 18, 18);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-out;
  margin-top: 50px;
}

.download-button:hover {
  background-color: rgba(255, 211, 0, 0.9);
}

@media screen and (max-width: 768px) {
  .skills-list {
    flex-wrap: nowrap; /* Add this line */
    flex-direction: column; /* Add this line */
  }
  
  .skills-list li {
    width: 100%; /* Add this line */
    box-sizing: border-box; /* Add this line */
  }
}






